.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.gray{
  background-color:rgb(134,147,151);
  height: 1vh;
  position: fixed;
  bottom: 4vh;
  width:100%;
}
.cowboysBlue{
  background-color: rgb(0, 34, 68);
  height: 4vh;
  position: fixed;
  bottom: 0;
  width:100%;
  text-align: center;
  color:white;
  font-size: 2vh;
}